/* eslint-disable */
<template>
  <div id="app">
    <list v-for="list in lists" :index="list"></list>
    <div class="add-list">
      <button type="button" class="btn btn-info btn-add-list" @click="addList">
        <i class="glyphicon glyphicon-trash"></i> Ajouter une liste
      </button>
    </div>
    <!-- <router-view /> -->
  </div>
</template>

<script>
import List from "./components/List";

export default {
  components: {
    List
  },
  data() {
    return {
      lists: []
    };
  },
  mounted() {
    this.lists = this.$children;
    this.lists.forEach((list, i) => {
      list.index = i;
    });
  },
  methods: {
    addList() {
      console.log(this.lists);

      this.lists++;
      console.log(this.lists);
    }
  },
  name: "listecourses"
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

body {
  background-color: #E5EEF2;

}

.add-list {
  float: left;
  width: 48%;
  align-content: center;
  border: dashed 2px black;
  vertical-align: middle;
  margin: 10px;

}
.btn-add-list {
  margin: 100px;
}
</style>



