/* eslint-disable */
<template>
  <section class="list rounded mb-0">
    <header class="header">
      <h3 class="title-list">Liste {{ index }} pour mes courses du {{ dateList }}</h3>
    
            <button class="destroy" @click.prevent="deleteList(index)">Supprimer cette liste</button>
    </header>
    <div class="main">
      <table class="table table-striped">
        <thead>
          <tr>
            <th scope="col">Article</th>
            <th scope="col">Quantité</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="article in articles">
            <td @click="editArticleName(article)">
              <input type="text" v-model="article.name" />
            </td>
            <td>
              <input type="text" v-model="article.quantity" />
            </td>
            <button class="destroy" @click.prevent="deleteArticle(article)">Supprimer</button>
          </tr>
        </tbody>
        <button type="button" class="btn btn-info btn-add-article " @click="addArticle">
          <i class="glyphicon glyphicon-trash"></i> Ajouter un article
        </button>
      </table>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    index: { type: Number, default: 0 }
  },
  data() {
    return {
      //   index: 0,
      dateList: "12/05/2020",
      articles: [
        {
          name: "pain",
          quantity: 4
        }
      ]
    };
  },
  methods: {
    addArticle() {
      this.articles.push({
        name: "",
        quantity: 0
      });
    },
    deleteArticle(article) {
      this.articles = this.articles.filter(i => i !== article);
    },
    editArticleName(article) {
      console.log(article);
    },
    deleteList(index) {
        
        console.log(this.$parent.lists);

        
    }
  }
};
</script>

<style>
.list {
  float: left;
  width: 48%;
  background-color: #87CEFA;
  display: block;
  margin: 10px;
  box-shadow: 6px 6px 6px black;
}
.btn-add-article {
    margin: 13px;
}
.title-list {
    margin-top: 10px;
}
.table th, .table td, .table th, .table td { border: none; }

</style>


